import { graphql } from "gatsby"
import React from "react"

const IndexPage = ({ data }) => {
  return (
    <div>
      {console.log(data)}
      {data.posts.nodes.map(post => (
        <div>
          <p>{post.title}</p>
          <img src={post.image.file.url}/>
          key={post.id}
        </div>
      ))}
    </div>
  )
}

export default IndexPage

export const query = graphql`
query PostQuery {
  posts: allContentfulPost {
    nodes {
      title
      image {
        id
        file {
          url
        }
      }
    }
  }
}
`
